'use strict';
// {
//   level:1,  //level=1 默认清除缓存 level=2 版本更新清除缓存 level=3 不清缓存
// 0      //进程会话缓存,收到内存警告或进程结束缓存失效
//   1        //存储缓存,缓存时效由storageTime控制,默认7天
//   2    //存储缓存,APP版本更新(覆盖安装)缓存失效
//   3 //存储缓存,APP卸载缓存失效
//     key:'key1',
//   data:'data1'
// }
import {WinJSDec} from './APP/config';

var name = 'STORAGE_SAVE';
var STORAGE_SAVE = (key, data, level = 1) => {
  WinJSDec(_ => {
    window.WinJSBridge.call('storage', 'save', {level, key, data});
  });
};
export default STORAGE_SAVE;
